@use "../mixins.scss" as *;
@use "../variables.scss" as *;

.invite-dealers {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 320px;

  &--secondary {
    background-color: $bgcolor-secondary;
  }

  @media (min-width: $tablet-width) {
    position: relative;
    width: 768px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
  }
}

.invite-dealers__wrapper {
  @include site-container;
  display: flex;
  align-items: center;
  padding-top: 26px;
  padding-bottom: 26px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    padding: 58px 70px;
    gap: 296px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 95px;
    left: 110px;
    box-sizing: border-box;
    gap: 87px;
    padding: 56px 60px 56px 80px;
    width: 565px;
    height: 198px;
    z-index: 1;
    background-color: #ffffff;
  }
}

.invite-dealers__title {
  @include site-title(16px, 20px);
  text-transform: uppercase;
  width: 50%;

  @media (min-width: $tablet-width) {
    @include site-title(20px, 26px);
  }
}

.invite-dealers__address {
  @include site-content (14px, 18px);
  width: 50%;

  @media (min-width: $tablet-width) {
    @include site-content (16px, 20px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
    text-align: right;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}

.invite-dealers__map-wrapper {
  position: relative;
  min-height: 362px;
  background: url("../img/map.png") no-repeat;
  width: 100%;

  @media (min-width: $tablet-width) {
    background: url("../img/map-tablet.png") no-repeat;
  }

  @media (min-width: $desktop-width) {
    background: url("../img/map-desktop.png") no-repeat;
  }
}

.invite-dealers__map-wrapper iframe {
  border: none;
  display: block;
  width: 100%;
  min-height: 362px;

  @media (min-width: $tablet-width) {
    min-height: 400px;
  }
}
